import Helper from '@/function.js';
export default {

	data() {

		return {
			//array: ['选择任务类型', '抖音', '快手', '微视', '火山'],
			array: ['选择任务类型', '抖音', '快手'],
			aim: ['选择任务要求', '点赞', '关注', '点赞+关注'],
			aimIndex: 0,
			index: 0,
			token: "",
			taskPackage: {},
			taskName: "",
			url: "",
			description: "",
			list: [],
			current: -1,
			packIndex:-1,

		}
	},
	onLoad: function(options) {
		this.token = uni.getStorageSync('token');
		this.taskPackage = JSON.parse(options.item);
		console.log(options.item)
		this.getList(this.taskPackage.taskPackageId);
	},

	methods: {
		/* 性别选择 */
		bindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value);
			this.index = e.target.value;
		},
		radioChange: function(evt) {
			this.packIndex = evt.target.value;
			for (let i = 0; i < this.list.length; i++) {
				
				if (this.list[i].id == evt.target.value) {
					this.current = i;
					console.log(i)
					break;
				}
			}
		},
		getList(id) {
			var _this = this;

			uni.request({
				url: _this.ApiGateWay + '/sale/Member/GetTaskPackageDetailsList?id=' + id,
				data: "",
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					console.log(res + '111');
					if (Helper.CheckData(res)) {

						_this.list = res.data.list;
						console.log(_this.list)

						// _this.setListStatus();
					} else {

					}


				},

				fail: function() {

				},
				complete: function() {
					uni.hideLoading();

				}
			});
		},
		bindAimPickerChange(e) {
			this.aimIndex = e.target.value;
		},
		goCreateTask() {
			var _this = this;

			//------判断

			// if (Helper.isEmpty(this.taskName))
			// {
			// 	uni.showToast({
			// 		title:"请输入任务名",
			// 		icon:"none"
			// 	})

			// 	return;
			// }
			if (this.index == 0) {
				uni.showToast({
					title: "请选择任务类型",
					icon: "none"
				})

				return;
			}
			// if (this.aimIndex == 0)
			// {
			// 	uni.showToast({
			// 		title:"请选择任务要求",
			// 		icon:"none"
			// 	})

			// 	return;
			// }

			if (Helper.isEmpty(this.url)) {
				uni.showToast({
					title: "请输入视频地址",
					icon: "none"
				})

				return;
			}
			
			if (this.packIndex == -1)
			{
				uni.showToast({
					title: "请选择数量",
					icon: "none"
				})
				
				return;
			}

			// if (Helper.isEmpty(this.description))
			// {
			// 	uni.showToast({
			// 		title:"请输入任务说明",
			// 		icon:"none"
			// 	})

			// 	return;
			// }

			var params = {
				orderNo: _this.taskPackage.orderNo,
				taskName: _this.taskName,
				url: _this.url,
				description: _this.description,
				aim: _this.aimIndex - 1,
				taskType: _this.index - 1,
				packIndex:_this.packIndex
			}
			uni.showLoading({
				title: "加载中..."
			})
			uni.request({
				url: _this.ApiGateWay + "/sale/Member/CreateTask",
				data: params,
				header: {
					Authorization: _this.token
				},
				method: 'POST',
				dataType: 'json',
				success: function(res) {

					if (Helper.CheckData(res)) {
						uni.showModal({
							title: "信息提示",
							content: "任务发布成功",
							showCancel: false,
							success: function(res) {
								uni.navigateBack({
									delta: 1
								})
							}
						})

					} else {
						uni.showModal({
							title: "出错了",
							content: res.data.errorMessage,
							showCancel: false,
							success: function(res) {

							}
						})
					}

				},
				complete() {
					uni.hideLoading();
				},
				fail: function(res) {
					uni.stopPullDownRefresh();
				}
			});
		},

	}

}
